.cards {
  display: flex;
  flex-wrap: wrap;
  width: 90vw;
  margin: auto;

  &__total {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    align-items: center;
    width: 100%;
    margin: auto;
    height: 16vh;
    margin-top: 2vh;
    border-radius: 5px;
    color: $color;
    background-color: $background;

    &--dark {
      color: $color-dark;
      background-color: $grayBackground-dark;
    }
  }

  &__h2 {
    width: 100%;
    text-transform: uppercase;
    font-size: 18px;
    color: gray;
  }

  &__p {
    width: 100%;
    font-weight: bold;
    font-size: 32px;
  }

  &__income,
  &__expense {
    position: relative;
    display: flex;
    text-align: center;
    align-items: center;
    flex-wrap: wrap;
    width: 47.5%;
    height: 14vh;
    margin-top: 2vh;
    border-radius: 5px;
    background-color: $background;

    &--dark {
      background-color: $grayBackground-dark;
    }

    &--main-card {
      width: 100%;
      height: 18vh;
    }

    h2 {
      width: 100%;
      text-transform: uppercase;
      font-size: 14px;
      color: gray;
    }

    p {
      width: 100%;
      font-weight: bold;
      font-size: 24px;
    }
  }

  &__expense {
    margin-right: 5%;

    &--main-card {
      margin-right: 0;
    }
  }

  &__income-icon {
    position: absolute;
    top: 14px;
    right: 14px;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    fill: green;
    background-color: lightgreen;
  }

  &__expense-icon {
    position: absolute;
    top: 14px;
    right: 14px;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    fill: red;
    background-color: lightcoral;
  }
}

@media (min-width: 1024px) {
  .cards {
    width: 70vw;
    margin: auto;
  }
}

@media (max-width: 600px) {
  .cards {
    &__income,
    &__expense {
      h2 {
        margin-top: 46px;
        width: 100%;
        text-transform: uppercase;
        font-size: 14px;
        color: gray;
      }

      p {
        width: 100%;
        font-weight: bold;
        font-size: 24px;
      }
    }
  }
}
